<script>
// gsap
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";
gsap.registerPlugin(ScrollTrigger);

// swiper
import { Swiper, SwiperSlide } from 'swiper/vue';
import 'swiper/css';
import 'swiper/css/pagination';
import { Pagination } from 'swiper/modules';

export default {
  name: 'AppGsap',

  components: {
    Swiper,
    SwiperSlide,
  },
  setup() {
    return {
      modules: [Pagination],
    };
  },



  mounted: function() {
    this.scrollAnimation();
  },

  methods: {
    scrollAnimation() {

      //상단타이틀
      gsap.to(".text_con1", {
        scrollTrigger: {
          trigger: '.section01',
          start: "0% 0%",
          end: "100% 50%",
          scrub: 1,
          markers: false,
        },  
        top: "-100%",
      });

      //상단서브타이틀
      gsap.from(".text_con2", {
        scrollTrigger: {
          trigger: '.section01',
          start: "top top",
          end: "bottom bottom",
          scrub: 1,
          markers: false,
        },  
        top: "30%",
        duration: 1
      });

      //상단 배경
      gsap.fromTo(".img_bg", 
      { opacity: 1, scale: 1 },
      { 
        scrollTrigger: {
          trigger: '.section01',
          start: "0% 0%",
          end: "bottom 60%",
          scrub: 0.5,
          markers: false,
        },
        scale: 1.2,
        opacity: 0,
      });

      //상단 무비
      gsap.fromTo(".section01 .img_con",
        { scale: 0.7 },
        {
          scale: 1,
          ease: "none",
          force3D: true,
          scrollTrigger: {
            pin: ".section01",
            trigger: ".section01",
            start: "0% 0%",
            end: "bottom top",
            scrub: 0.5,
            markers: false,
          },
          y: -100,
          borderRadius: 50,
        }
      );

      //스크롤시 교차되는 이미지
      const boxScroll = gsap.timeline({
        scrollTrigger: {
          trigger: '.section02',
          start: "0% 30%",
          end: "100% 80%",
          scrub: 1,
          markers: false
        }
      });
      boxScroll.to(".box2", {y: 100, duration: 1})
      boxScroll.to(".box3", {y: -100})

      //스크롤시 가로흐름 이미지
      let flow1 = gsap.utils.toArray(".flow1 .panel");
      gsap.to(flow1, {
        xPercent: -100 * (flow1.length - 1),
        ease: "none",
        scrollTrigger: {
          trigger: ".section03",
          // pin: true,
          scrub: 1,
          // snap: 1 / (flow1.length - 1),
          markers: false,
          start: "0% 30%",
          // end: () => "+=" + document.querySelector(".section03").offsetWidth
        }
      });

      let flow2 = gsap.utils.toArray(".flow2 .panel");
      gsap.to(flow2, {
        xPercent: 100 * (flow1.length - 1),
        ease: "none",
        scrollTrigger: {
          trigger: ".section03",
          // pin: true,
          scrub: 1,
          // snap: 1 / (flow1.length - 1),
          markers: false,
          start: "0% 30%",
          // end: () => "+=" + document.querySelector(".section03").offsetWidth
        },
        duration: 1
      });

      //모달팝업
      const modalOpen = document.querySelectorAll(".btn_open");
      const modalClose = document.querySelectorAll(".modal_wrap .btn_close");
      var targetID;

      modalOpen.forEach( number => {
        number.addEventListener('click', function(){
          targetID = this.getAttribute('href');
          document.querySelector(targetID).classList.add('is_active');
        });
      });

      modalClose.forEach( number => {
        number.addEventListener('click', function(){
          this.parentNode.parentNode.classList.remove('is_active');
        });
      });

    }
  }

};
</script>


